<template>
  <div class="detail">
    <DetailHeader :title="playlist.name"></DetailHeader>
    <DetailTop :path="playlist.coverImgUrl" ref="top"></DetailTop>
    <div class="bottom">
      <ScrollView ref='scrollview'>
         <DetailBottom :playlist="playlist.tracks"></DetailBottom>
      </ScrollView>
    </div>
  </div>
</template>

<script>
import DetailHeader from '../components/Detail/DetailHeader';
import DetailTop from '../components/Detail/DetailTop';
import DetailBottom from '../components/Detail/DetailBottom';
import ScrollView from '../components/ScrollView';
import {getPlayList,getAlbum} from '../api/index';
export default {
  name: 'Detail',
  components:{
    DetailHeader,
    DetailTop,
    DetailBottom,
    ScrollView,
  },
  data() { 
    return {
      playlist:{},
    }
  },
  created(){
    // console.log(this.$route.params.type);看看是personalized还是albums
    if(this.$route.params.type==='personalized'){
  //吧id传给get方法从服务器获取playlist
      getPlayList({id: this.$route.params.id})
      .then((data)=>{
        // console.log(data);
        this.playlist=data.playlist;
      })
      .catch((err)=>{
        console.log(err);
      })
    }else if(this.$route.params.type==='albums'){
      getAlbum({id: this.$route.params.id})
       .then((data)=>{
          // console.log(data);
          this.playlist={
            name:data.album.name,
            coverImgUrl:data.album.picUrl,
            tracks:data.songs,
          }
      })
      .catch((err)=>{
         console.log(err);
      })
    }
   
  },
  mounted(){
    let defaultHeight=this.$refs.top.$el.offsetHeight;
    // console.log(defaultHeight); 
    this.$refs.scrollview.scrolling((offsetY)=>{
      // console.log(offsetY);
      if(offsetY>0){
        // console.log('向下滚');
          let scale=1+offsetY/defaultHeight;
          this.$refs.top.$el.style.transformOrigin="600px 0";
          this.$refs.top.$el.style.transform=`scale(${scale})`;
      }else{
        // console.log('向上滚');
          // let scale=10*Math.abs(offsetY)/defaultHeight;
          //高斯模糊特别耗费性能
          // this.$refs.top.$el.style.filter=`blur(${scale}px)`;
           let scale=Math.abs(offsetY)/defaultHeight;
           this.$refs.top.changeMask(scale);
      
      }
    })
  }
 }
</script>

<style lang="scss" scoped>
@import "../assets/css/mixin";
.detail{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  @include bg_sub_color();
  // background-color: red;
  // z-index:1999;
  .bottom{
    position: fixed;
    top: 500px;
    bottom: 0;
    left: 0;
    right: 0;
  }
}

</style>