<template>
      <ul class="detail-bottom">
          <li class="bottom-top"  @click="selectAllMusic">
              <div class="bottom-icon"></div>
              <div class="bottom-title">播放全部</div>
          </li>
          <li v-for="value in playlist" :key="value.id" class="item" @click="selectMusic(value.id)">
              <h3>{{value.name}}</h3>
              <p>{{value.ar[0].name}}-{{value.al.name}}</p>
          </li>
      </ul>
</template>

<script>
import {mapActions} from 'vuex'
export default {
  name: 'DetailBottom',
  methods:{
       ...mapActions([
           'setFullScreen',
           'setMiniPlayer',
           'setSongDetail'
       ]),
      selectMusic(id){
          //触发actions里的setFullScreen方法
        //   this.$store.dispatch('setFullScreen',true);
        this.setFullScreen(true);
        this.setMiniPlayer(false);
        this.setSongDetail([id]);
      },
     selectAllMusic(){
        let ids=this.playlist.map(function(item){
            return item.id;
        })
        this.setFullScreen(true);
        this.setMiniPlayer(false);
        this.setSongDetail(ids);

     }
  },
  props:{
      playlist:{
          type:Array,
          default:()=>[],
          required:true
      }
  }
 }
</script>

<style lang="scss" scoped>
@import '../../assets/css/mixin';
// @import '../../assets/css/variable';
.detail-bottom{
    width: 100%;
    li{
        width: 100%;
        height: 100px;
        box-sizing: border-box;
        padding: 20px;
        @include bg_sub_color();
        border-bottom: 1px solid #ccc;
    }
    .item{
        h3{
            @include font_color();
            @include font_size($font_medium);
            @include clamp(1);

        }

        p{
            margin-top: 5px;
            // @include font_color();
            color:rgb(136, 136, 136);
            @include font_size($font_small);
            @include clamp(1);
         }
    }
    .bottom-top{
        display: flex;
        text-align: center;
        border-top-left-radius: 50px;
        border-top-right-radius: 50px;
        .bottom-icon{
            width: 60px;
            height: 60px;
            @include bg_img("../../assets/images/small_play");
            margin-right: 30px;
        }
        .bottom-title{
            line-height: 60px;
            @include font_size($font_large);
            @include font_color();
        }
    }
}
</style>