<template>
  <div class="detail-top">
      <div class="img-container">
          <img :src="path" alt="">
          <div class="mask" ref="mask"></div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'DetailTop',
  props:{
        path:{
            type:String,
            default:'',
            required:true
        }
   },
   methods:{
       changeMask(data){
           this.$refs.mask.style.opacity=data;
        //    console.log(this.$refs.mask.$el.style.opacity);
       }
   }
 }
</script>

<style lang="scss" scoped>
.detail-top{
    width: 100%;
    height: 500px;
    overflow: hidden;
    position: relative;
    img{
        width: 100%;        
    }
    .mask{
        position: absolute;
        top: 0;
        left: 0;
        background-color: black;
        width: 100%;
        height: 100%;
        opacity: 0;

    }
}
</style>