<template>
  <div class="header"  @click="changeTheme">
      <div class="header-left" @click.stop="back"></div>
      <p class="header-title">{{title}}</p>
      <div class="header-right"></div>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  data(){
    return{
      theme:['theme','theme1','theme2'],
      index:0,
    }
  },
  props:{
      title:{
          type:String,
          default:'',
          required:true
          },
      
  },
  methods:{
    back(){
        window.history.back();
    },
    changeTheme(){
      this.index++;
      if(this.index>=this.theme.length){
        this.index=0;
      }
      document.documentElement.setAttribute('data-theme',this.theme[this.index]);

    }
  }
 }
</script>

<style lang="scss" scoped>
@import '../../assets/css/variable';
@import '../../assets/css/mixin';
.header{
  //本来想用z-index来设头部在最上面，但是歌单详情设z-index盖不住头部。从父现象。
  //头部里的和detail比？？？？
  //改用.recommend超出不显示
    // position: relative;
    // z-index: 999;
    width:100%;
    height:100px;
    @include bg_color();
    display:flex;
    justify-content:space-between;
    .header-left, .header-right{
        width:84px;
        height:84px;
        // background:black;
        margin-top:8px;
    }
     .header-left{
       @include bg_img('../../assets/images/back');
     }
     .header-right{
        @include bg_img('../../assets/images/more');

     }
    .header-title{
        text-align:center;
        line-height:100px;
        @include font_size($font_medium);
        color:white;
        @include no-wrap();
    }
}

</style>